import React, { Component } from 'react';
import './Todo.css';

class Todo extends Component {
    constructor(props){
        super(props);
        this.state = {
            todoText: props.todoText
        }
    }
    render() {
    return (
        <div className = "list-item">
            <input type="checkbox" name = "checkbox" className ="checkbox" value = "1"></input>
            <p>{this.state.todoText}</p>
            <button className = "delete-button">delete</button>
        </div>
    );
  }
}

export default Todo;
